<template>
  <div class="headTop">
    <div class="logo-div">
      <span @click="$router.push({path:'/managehomePage'})">后台管理系统</span>
    </div>
    <div class="admin">
      <el-dropdown trigger="click" class="md-admin-drop" @command="selectCommand">
        <span class="el-dropdown-link">{{admin.adminName}}</span>
        <div class="head_img">
          <img :src="admin.avatar" />
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="management">个人信息</el-dropdown-item>
          <el-dropdown-item command="logout">退出登陆</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      admin: {}
    };
  },
  methods: {
    // goHomePage(){

    // },
    goto(val) {
      if (val == "recruitmentModel") {
        this.$router.push({ path: "/recruitmentModel" });
      } else {
        this.$router.push({ path: "/representModel" });
      }

      // console.log(val);
    },
    selectCommand(command) {
      if (command == "management" && this.admin.adminType == 1) {
        window.location.href = `${process.env.BASE_URL}manageStage.html#/adminInfo`;
      } else {
        sessionStorage.clear(); //清除sessionStorage
        window.location.href = `${process.env.BASE_URL}entering.html#/login`;
      }
    }
  },
  created() {
    this.admin = JSON.parse(sessionStorage.getItem("adminInfo"));
    // this.admin.avatar = window.URL.createObjectURL(this.admin.avatar)
    // console.log(this.admin.avatar)
  }
};
</script>
<style lang="scss" scoped>
.headTop {
  position: relative;
  width: 1200px;
  height: 50px;
  margin: 0px auto;
  border-bottom: 1px solid #e1e5ee;
  background-image: linear-gradient(45deg, #91bbee, #29eee3 50%, #91bbee);
  // background: #85b8fa;
  div {
    display: inline-block;
  }
  .logo-div {
    width: 150px;
    height: 100%;
    cursor: pointer;
    span{
        font-size: 20px;
        line-height: 50px;
        margin-left: 20px;
    }
  }
  .baner-title {
    span {
      margin-left: 25px;
      cursor: pointer;
    }
  }
  .admin {
    float: right;
    margin-top: 10px;
    margin-right: 20px;

    .el-dropdown-link {
      cursor: pointer;
    }
    .el-dropdown .el-dropdown-link,
    .el-dropdown .head_img {
      vertical-align: middle;
      height: 100%;
    }
    .head_img {
      height: 30px;
      margin-left: 10px;
    }
    .head_img img {
      width: 30px;
      height: 30px;
      border-radius: 50px;
    }
  }
}
</style>
