import Vue from 'vue'
import Router from 'vue-router'
import {
    getCookie,
    setCookie
} from '../../utils/utils.js'
import {
    adminInfo
} from 'os';
// 系统首页
const managehomePage = () => import('./views/home/index.vue');
const userModel = () => import('./views/mamageModel/userModel/index.vue');
const attachModel = () => import('./views/mamageModel/attachModel/index.vue');
const studentBasic = () => import('./views/mamageModel/studentModel/studentBasic/index.vue');
const studentEmploy = () => import('./views/mamageModel/studentModel/studentEmploy/index.vue');
const counselorModel = () => import('./views/mamageModel/counselorModel/index.vue');
const careerGuidModel = () => import('./views/mamageModel/careerGuidModel/index.vue');
const presentModel = () => import('./views/mamageModel/presentModel/index.vue');
/**
 * 重写路由的push方法
 */
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
    return routerPush.call(this, location).catch(error => error)
}
Vue.use(Router)



const router = new Router({
    // mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            redirect: '/managehomePage',
            meta: {
                requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
            },
        },
        {
            path: '/managehomePage',
            name: 'managehomePage',
            component: managehomePage,
            meta: {
                title: '就业信息管理系统首页', // 页面标题
                requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
            },
            children: [
                {
                    path: '/userModel',
                    name: 'userModel',
                    component: userModel,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "用户管理" // 页面标题
                    }
                },
                {
                    path: '/attachModel',
                    name: 'attachModel',
                    component: attachModel,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "院系管理" // 页面标题
                    }
                },
                {
                    path: '/studentBasic',
                    name: 'studentBasic',
                    component: studentBasic,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "学生基本信息" // 页面标题
                    }
                },
                {
                    path: '/studentEmploy',
                    name: 'studentEmploy',
                    component: studentEmploy,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "学生就业信息" // 页面标题
                    }
                },
                {
                    path: '/counselorModel',
                    name: 'counselorModel',
                    component: counselorModel,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "辅导员管理" // 页面标题
                    }
                },
                {
                    path: '/careerGuidModel',
                    name: 'careerGuidModel',
                    component: careerGuidModel,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "就业指导" // 页面标题
                    }
                },
                {
                    path: '/presentModel',
                    name: 'presentModel',
                    component: presentModel,
                    meta: {
                        requireAuth: true, // 是否需要登录的
                        title: "就业代表" // 页面标题
                    }
                }
            ]
        },
    ]
})

/**
 * @param {string} url url参数
 * @param {string} param 要检测的参数
 * @param {RegExp} reg 要检测的参数的正则
 * @param {string=} routerAdd 路由地址，可选
 * @description 检查参数是否在路由前面
 * */
function checkQuery(url, param, reg, routerAdd) {
    let checkArr = url.split(param)
    let routerParam = /#\/[\w]{1,}/
    if (routerParam.test(checkArr[1]) || !routerParam.test(url)) {
        let query = reg.exec(url)[0]
        location.href = location.origin + location.pathname + '#' + routerAdd + query
    }
}

// 路由拦截
router.beforeEach((to, from, next) => {
    checkQuery(location.href, '?access_token=', /\?access_token=[\w\-@]{1,}/, to.path);
    // 设置全局标题
    document.title = to.meta.title || '就业系统'
    const cookeiToken = location.href.split('access_token=')[1];
    const adminInfo = sessionStorage.getItem('adminInfo')
    // const cookie = getCookie('access-token')
    // const cookie = "d4ac87204e9990a07374bd9b103cfc7361567597021"
    if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
        if (adminInfo) { // 
            next();
        } else {
            if (cookeiToken) {
                setCookie('access-token', cookeiToken);
                next();
            } else {
                window.location.href = `${process.env.BASE_URL}entering.html#/adminLogin`;
                // window.location.href = `${process.env.BASE_URL}entering.html?rURL=${encodeURIComponent(window.location.href)}`;
            }
        }
    } else {
        next();
    }
})

export default router